import { Component, HostListener } from '@angular/core';
import { GlobalModel } from './model/global.model';
//import ApiService from './api/api.service';
import{ HttpClient } from "@angular/common/http";


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {
  title = 'covidTracker';
  global: boolean;
  country: string;
  data: GlobalModel;
  dailyData: any[];
  countries: any[];
  lineChartData: any[] = [
    {
      data: [65, 64, 33, 44], label: 'Temp label'
    }
  ];
  lineChartType = 'line';
  lineChartLabels: any[] = [
    'Label01', 'Label01', 'Label03'
  ];
  barChartType = 'bar';
  barChartLabels: any[] = [
    'Infected', 'Recovered', 'Deaths'
  ];
  barChartData: any[] = [
    { data: [65, 76, 33], label: 'Lable' }
  ];
  baseUrl = 'https://covid19.mathdro.id/api';

  

  constructor(private http: HttpClient) {


    this.data = new GlobalModel();
  }

  ngOnInit(): void {
    this.global = true;
    this.fetchData();
    this.fetchCountries();
    this.fetchDailyData();
  }

  fetchData() {
    this.http.get(this.baseUrl).subscribe((res: any[]) => {
      console.log(res);
      this.data.confiremd = res['confirmed']['value'];
      this.data.recovered = res['recovered']['value'];
      this.data.deaths = res['deaths']['value'];
      this.data.lastupdate = res['lastUpdate'];
      console.log(this.data.confiremd);
    });
  }
 
  fetchCountries() {
    this.http.get(this.baseUrl + '/countries').subscribe((res: any[]) => {
      console.log(res);
      var countries = res['countries'];
      this.countries = countries.map((name) => name['name']);
    });
  }


  fetchDataByCountry(country: string) {
    this.http.get(this.baseUrl + '/countries/' + country).subscribe((res: any[]) => {
      console.log(res);
      this.data.confiremd = res['confirmed']['value'];
      this.data.recovered = res['recovered']['value'];
      this.data.deaths = res['deaths']['value'];
      this.data.lastupdate = res['lastUpdate'];

      this.barChartData = [
        {
          data: [this.data.confiremd, this.data.recovered, this.data.deaths],
          label: 'People',
          barThickness: 50,
          backgroundColor: ['rgb(141, 50, 168)','rgb(50, 168, 105)','rgb(173, 37, 24)']
        }
      ];
    });
  }

  fetchDailyData() {
    this.http.get(this.baseUrl + '/daily').subscribe((res: any[]) => {
      console.log(res);
      this.lineChartLabels = res.map((date) => date['reportDate']);
      var infectedData = res.map((confirmed) => confirmed['totalConfirmed']);
      var deaths = res.map((deaths) => deaths['deaths']['total']);
      var recovered = res.map((rev) => rev);

      this.lineChartData = [
        {
          data: infectedData,
          label: 'Infected',
          backgroundColor:'rgb(50, 168, 105)'
        },
        {
          data: deaths,
          label: 'Deaths',
          backgroundColor:'rgb(173, 37, 24)'
        }
      ];
    });
  }

  countryChanged(value: string) {
    this.country = value;
    if(value == 'global') {
      this.fetchData();
      this.global = true;
    } else {
      this.fetchDataByCountry(value);
      this.global = false;
    }
  }

  // fetchDataByCountry(country: string) {
  //   return this.http.get(this.baseUrl + '/countries/' + country);
  // }

  // fetchDailyData() {
  //   return this.http.get(this.baseUrl + '/daily');
  // }

  // fetchCountries() {
  //   return this.http.get(this.baseUrl + '/countries');
  // }

  @HostListener('window:scroll', ['$event'])

onWindowScroll() {
    let element = document.querySelector('.navbar') as HTMLElement;
    if (window.pageYOffset > element.clientHeight) {
      element.classList.add('navbar-inverse');
    } else {
      element.classList.remove('navbar-inverse');
    }
  }

}